import MoviesDB from '../data/MoviesDB';
import dataRender from '../utility/dataRender';

const Trending = {
  render() {
    return `
      <div class="container-content">
        <slider-tag id="upcomming"></slider-tag>
        <list-tag id="trending-movies"></list-tag>
      </div>
      <aside>
        <aside-tag id="trending-tv-show"></aside-tag>
      </aside>
    `;
  },

  async afterRender() {
    const upcommingContainer = document.querySelector('#upcomming');
    const trendingMoviesContainer = document.querySelector('#trending-movies');
    const trendingTvContainer = document.querySelector('#trending-tv-show');

    new dataRender({
      data: MoviesDB.Upcomming(),
      container: upcommingContainer,
    }).render();

    new dataRender({
      title: 'Trending Movies',
      data: MoviesDB.Trending(),
      container: trendingMoviesContainer,
      layouts: 'grid',
      pagination: true,
      paginationName: 'trending',
    }).render();

    new dataRender({
      title: 'Trending Tv Shows',
      data: MoviesDB.Trending('1', 'tv'),
      container: trendingTvContainer,
    }).render();
  },
};

export default Trending;
