/* eslint-disable no-param-reassign */
import MoviesDB from '../data/MoviesDB';
import urlParse from '../routes/urlParse';
import dataRender from '../utility/dataRender';

const Genre = {
  render() {
    const url = urlParse.parseUrlWithoutCombiner();
    if (url.pages === 'on-air-tv') {
      return `
        <div class="container-content">
            <list-tag id="on-air-tv"></list-tag>
        </div>
        <aside>
          <aside-tag id="now-playing-movies" class="now-playing"></aside-tag>
        </aside>
      `;
    }
    if (url.pages === 'top-rated-tv') {
      return `
        <div class="container-content">
            <list-tag id="top-rated-tv"></list-tag>
        </div>
        <aside>
          <aside-tag id="top-rated-movies" class="top-rated-movies"></aside-tag>
        </aside>
      `;
    }
    if (url.pages === 'popular-tv') {
      return `
        <div class="container-content">
            <list-tag id="popular-tv"></list-tag>
        </div>
        <aside>
          <aside-tag id="popular-movies"></aside-tag>
        </aside>
      `;
    }
    return `
        <div class="container-content">
            <genre-tag id="genre-list"></genre-tag>
            <list-tag id="genre-tag" class="genre"></list-tag>
        </div> 
        <aside>
          <aside-tag id="trending-movies" class="trending-movies"></aside-tag>
        </aside>
    `;
  },

  async afterRender() {
    const url = urlParse.parseUrlWithoutCombiner();
    const genreContainer = document.querySelector('.genre');
    const genreListContainer = document.querySelector('#genre-list');
    const trendingContainer = document.querySelector('.trending-movies');
    const onAirContainer = document.querySelector('#on-air-tv');
    const asideContainer = document.querySelector('aside-tag');
    const topRatredContainer = document.querySelector('#top-rated-tv');
    const popularTvContainer = document.querySelector('#popular-tv');

    if (url.pages === 'on-air-tv') {
      new dataRender({
        data: MoviesDB.TvOnAir(),
        container: onAirContainer,
        title: 'On Air Tv',
        layouts: 'grid',
        pagination: true,
        paginationName: 'on-air-tv',
      }).render();

      new dataRender({
        data: MoviesDB.MoviesNowPlaying(),
        container: asideContainer,
        title: 'Now Playing Movies',
      }).render();
      return;
    }

    if (url.pages === 'top-rated-tv') {
      new dataRender({
        data: MoviesDB.TopRated(1, 'tv'),
        container: topRatredContainer,
        title: 'Top Rated Tv Shows',
        layouts: 'grid',
        pagination: true,
        paginationName: 'top-rated-tv',
      }).render();

      new dataRender({
        data: MoviesDB.TopRated(),
        container: asideContainer,
        title: 'Top Rated Movies',
      }).render();
      return;
    }

    if (url.pages === 'popular-tv') {
      new dataRender({
        data: MoviesDB.PopularMovieOrTv(1, 'tv'),
        container: popularTvContainer,
        title: 'Popular Tv Shows',
        layouts: 'grid',
        pagination: true,
        paginationName: 'popular-tv',
      }).render();

      new dataRender({
        data: MoviesDB.PopularMovieOrTv(),
        container: asideContainer,
        title: 'Popular Movies',
      }).render();
      return;
    }

    const titles = await MoviesDB.spesificGenre(url.id);

    new dataRender({
      data: MoviesDB.Genre(),
      container: genreListContainer,
      title: 'Genre List',
    }).render();

    new dataRender({
      data: MoviesDB.MovieOrTvByGenre(url.id),
      container: genreContainer,
      layouts: 'grid',
      pagination: true,
      paginationName: 'genre-list',
      title: titles,
    }).render();

    new dataRender({
      data: MoviesDB.Trending(),
      container: trendingContainer,
      title: 'For You',
    }).render();
  },
};

export default Genre;
