import MoviesDB from '../data/MoviesDB';
import '../../components/Slider';
import '../../components/List';
import '../../components/Genre';
import dataRender from '../utility/dataRender';

const Home = {
  render() {
    return `
            <div class="container-content">
              <slider-tag id="slider"></slider-tag>
              <genre-tag id="genre"></genre-tag>
              <list-tag id="upcomming"></list-tag>
              <list-tag id="popular-actor"></list-tag>
              <list-tag id="now-playing-movies"></list-tag>
              <list-tag id="top-rated-movies"></list-tag>
            </div>
            <aside class="">
              <aside-tag id="trending-tv"></aside-tag>
            </aside>          
          `;
  },

  afterRender() {
    const sliderContainer = document.querySelector('#slider');
    const genreContainer = document.querySelector('#genre');
    const upcommingContainer = document.querySelector('#upcomming');
    const popularActorContainer = document.querySelector('#popular-actor');
    const nowPlayingContainer = document.querySelector('#now-playing-movies');
    const trendingTvContainer = document.querySelector('#trending-tv');
    const topRatedContainer = document.querySelector('#top-rated-movies');

    new dataRender({
      title: 'Trending Movies',
      data: MoviesDB.Trending(),
      container: sliderContainer,
    }).render();

    new dataRender({
      title: 'Trending Tv Show',
      data: MoviesDB.Trending('1', 'tv'),
      container: trendingTvContainer,
    }).render();

    new dataRender({
      title: 'Genre List',
      data: MoviesDB.Genre(),
      container: genreContainer,
    }).render();

    new dataRender({
      title: 'Upcomming Movies',
      data: MoviesDB.Upcomming(),
      container: upcommingContainer,
      layouts: 'flex',
      totalData: 10,
      link: true,
      slug: 'upcomming',
    }).render();

    new dataRender({
      title: 'Popular Actors',
      data: MoviesDB.Actor(),
      container: popularActorContainer,
      layouts: 'flex',
      totalData: 10,
    }).render();

    new dataRender({
      title: 'Now Playing Movies',
      data: MoviesDB.MoviesNowPlaying(),
      container: nowPlayingContainer,
      layouts: 'grid',
      pagination: true,
      paginationName: 'now-playing',
    }).render();

    new dataRender({
      title: 'Top Rated Movies',
      data: MoviesDB.TopRated(),
      container: topRatedContainer,
      layouts: 'flex',
      totalData: 10,
      link: true,
      slug: 'top-rated-movies',
    }).render();
  },
};

export default Home;
