import { detailMovieOrTvTemplate, detailPerson, seasonTemplate } from '../../templates/templateCreator';
import MoviesDB from '../data/MoviesDB';
import urlParse from '../routes/urlParse';
import dataRender from '../utility/dataRender';

const Detail = {
  render() {
    const url = urlParse.parseUrlWithoutCombiner();
    if (url.season) {
      return `
        <div class="container-content mb-5">
          <div class="content"></div>
        </div>
        <aside>
          <aside-tag id="for-you"></aside-tag>
        </aside>
      `;
    }
    if (url.pages === 'person') {
      return `
        <div class="container-content">
          <div class="content mb-5">
          </div>
          <list-tag id="movies-by-this-actor" class="movies-actor"></list-tag>
          <list-tag id="tv-show-by-this-actor" class="tv-actor"></list-tag>
        </div>
        <aside>
            <aside-tag id="for-you"></aside-tag>
        </aside>
    `;
    }
    return `
        <div class="container-content">
          <div class="content">
          </div>
          <list-tag id="similar"></list-tag>
        </div>
        <aside>
            <aside-tag id="for-you"></aside-tag>
        </aside>
    `;
  },

  async afterRender() {
    const container = document.querySelector('.content');
    const recommendationContainer = document.querySelector('#for-you');
    const similarContainer = document.querySelector('#similar');
    const moviesActorContainer = document.querySelector('.movies-actor');
    const tvActorContainer = document.querySelector('.tv-actor');
    const url = urlParse.parseUrlWithoutCombiner();

    if (url.pages === 'person') {
      const dataPerson = await MoviesDB.DetailActor(url.id);

      container.innerHTML = detailPerson(dataPerson);

      new dataRender({
        data: MoviesDB.MoviesOrTvByActor(url.id),
        container: moviesActorContainer,
        title: 'All Movies By This Actor',
      }).render();

      new dataRender({
        data: MoviesDB.MoviesOrTvByActor(url.id, 'tv'),
        container: tvActorContainer,
        title: 'All TV Shows By This Actor',
      }).render();

      new dataRender({
        data: MoviesDB.Trending(1, 'tv'),
        container: recommendationContainer,
        title: 'For You',
      }).render();

      return;
    }

    if (url.idSeason) {
      const data = await MoviesDB.SeasonDetail(url.id, url.idSeason);

      container.innerHTML = seasonTemplate(data);

      new dataRender({
        data: MoviesDB.Recommendation(url.id, 'tv'),
        container: recommendationContainer,
        title: 'For You',
      }).render();

      return;
    }

    const data = await MoviesDB.Detail(url.id, url.pages);
    const trailers = await MoviesDB.Trailer(url.id, url.pages);
    const cast = await MoviesDB.CreditMovieOrTv(url.id, url.pages);

    container.innerHTML = detailMovieOrTvTemplate(data, cast, trailers);

    document.querySelector('.list-card').style.width = `${158 * `${cast.length < 10 ? cast.length : 10}`}px`;

    new dataRender({
      data: MoviesDB.Recommendation(url.id, url.pages),
      container: recommendationContainer,
      title: 'For You',
    }).render();

    new dataRender({
      data: MoviesDB.Similar(url.id, url.pages),
      container: similarContainer,
      title: `Similar ${String(url.pages).charAt(0).toUpperCase() + String(url.pages).slice(1)}`,
      totalData: 10,
    }).render();
  },
};

export default Detail;
